import { useEffect } from "react";
import { AMLApplicationIntro } from "../../components/AMLApplicationIntro/AMLApplicationIntro";
import { CDLHybridApplicationIntro } from "../../components/CDLHybridApplicationIntro/CDLHybridApplicationIntro";
import { FiggForFinoramicIntro } from "../../components/FiggForFinoramicIntro/FiggForFinoramicIntro";
import { HomeFooter } from "../../components/HomeFooter/HomeFooter";
import { Intro } from "../../components/Intro/Intro";
import { Topbar } from "../../components/Topbar/Topbar";
import { TrainingTestTrackerIntro } from "../../components/TrainingTestTrackerIntro/TrainingTestTrackerIntro";

export const Home = ({location}) => {
    useEffect(() => {
        window.scrollTo(0, 0)
    }, [])

    useEffect(() => {
        if (location.hash) {
            const target = document.getElementById(location.hash.replace(/^#/, ""))
            
            if (target) {
                target.scrollIntoView({
                    behavior: "smooth"
                })
            }
        }
    }, [location])

    return (
        <div>
            <Topbar />
            <Intro />
            <span id="work" />
            <CDLHybridApplicationIntro />
            <AMLApplicationIntro />
            <FiggForFinoramicIntro />
            <TrainingTestTrackerIntro />
            <HomeFooter />
        </div>
    )
}