import { Fragment, useEffect, useRef, useState } from "react";
import { useHistory } from "react-router";
import { Link } from "react-router-dom";
import { PageContentSection } from "../../components/PageContentSection/PageContentSection";
import { Topbar } from "../../components/Topbar/Topbar";
import { doLogin, getAssetDownloadURL } from "../../firebase";
import "./Login.css"

export const Login = () => {
    const inputRef = useRef()
    const history = useHistory()
    const [viewImgUrl, setViewImgUrl] = useState("")
    const [hasLoginFailed, setHasLoginFailed] = useState(false)
    const [viewPassword, setViewPassword] = useState(false)
    const [loginInProgress, setLoginInProgress] = useState(false)

    useEffect(() => {
        getAssetDownloadURL("/Assets/Home/Others/View.png")
            .then(setViewImgUrl)
            .catch(error => {
                console.debug("Failed to get assest", error)
            })
    }, [])

    const startLogin = async () => {
        if (loginInProgress) {
            return
        }

        try {
            setLoginInProgress(true)
            setHasLoginFailed(false)
            const accessToken = inputRef.current.value
            await doLogin(accessToken)
            setLoginInProgress(false)
            history.push(history.location.pathname)
        } catch (error) {
            setHasLoginFailed(true)
            setLoginInProgress(false)
            inputRef.current.focus()
        }
    }

    const inputKeyPressHandler = (event) => {
        hasLoginFailed && setHasLoginFailed(false)
        if (event.key === "Enter") {
            event.preventDefault()
            return startLogin()
        }
    }
    
    const toggleView = () => setViewPassword(prev => !prev)
    const inputType = viewPassword ? "text" : "password"

    return (
        <Fragment>
            <Topbar showHome />
            <PageContentSection>
                <div className="flex w-full justify-center lg:-mt-96 sm:items-center border-box">
                    <div className="w-98 justify-start">
                        <div>Enter password</div>
                        <div className={"textbox-wrapper flex" + (hasLoginFailed ? " loginFailed" : "")}>
                            <input type={inputType} className="textbox" ref={inputRef} disabled={loginInProgress}
                                onKeyPress={inputKeyPressHandler}
                            />
                            <img className="view-img" src={viewImgUrl} alt="View password" onClick={toggleView}/>
                        </div>
                        <div className={"text-red-500 text-xs" + (!hasLoginFailed ? " invisible" : "")}>
                            Invalid password. <span className="text-white">
                                <Link to={{
                                    pathname: "/",
                                    hash: "#contactme"
                                }}>
                                    Let me know.
                                </Link>
                            </span>
                        </div>
                        <div className="text-center text-white text-4xl mt-1 cursor-pointer" onClick={startLogin}>
                            Next &rarr;
                        </div>
                    </div>
                </div>
            </PageContentSection>
        </Fragment>
    )
}