import { BrowserRouter, Switch, Route } from "react-router-dom";
import { AMLInvestigationSolution } from "./pages/AMLInvestigationSolution/AMLInvestigationSolution";
import { ConsumerDurableLoans } from "./pages/ConsumerDurableLoans/ConsumerDurableLoans";
import { Home } from './pages/Home/Home'
import {ApplicationContext} from './ApplicationContext'
import { useEffect, useState } from "react";
import { ProtectedRoute } from "./components/ProtectedRoute";
import firebase from './firebase'

export const App = () => {
    const [loggedInUser, setLoggedInUser] = useState(null)
    const [authInProgress, setAuthInProgress] = useState(false)
    const context = { loggedInUser, setLoggedInUser, authInProgress }

    useEffect(() => {
        setAuthInProgress(true)
        firebase.auth().onAuthStateChanged(user => {
            if (user) {
                setLoggedInUser(user)
            } else {
                setLoggedInUser(null)
            }
            setAuthInProgress(false)
        }, error => setAuthInProgress(false))
    }, [])

    return (
        <ApplicationContext.Provider value={context}>
            <BrowserRouter>
                <Switch>
                    <Route exact path={"/"} component={Home}/>
                    <ProtectedRoute exact path={"/aml"} component={AMLInvestigationSolution}/>
                    <ProtectedRoute exact path={"/cdl"} component={ConsumerDurableLoans}/>
                </Switch>
            </BrowserRouter>
        </ApplicationContext.Provider>
    )
}