import { useState } from "react"
import { Link } from "react-router-dom"
import { getAssetDownloadURL } from "../../firebase"


const RESUME_PATH = "/Assets/Home/Others/Surojit Gupta - Resume.pdf"

export const Topbar = ({ showHome = false }) => {
    const [resumeLink, setResumeLink] = useState("")
    
        useState(() => {
            getAssetDownloadURL(RESUME_PATH)
                .then(resumeDownloadUrl => setResumeLink(resumeDownloadUrl))
            .catch(error => console.debug("Failed to fetch resume", error))
        }, [])
    
    return (
        <div className="fixed w-full" style={{
            backgroundColor: "#121018"
        }}>
            <div className="flex">
                {
                    showHome &&
                    (
                        <div className="flex text-gray-50 text-lg mt-12 ml-14 pr-36 box-border">
                            <Link to="/">
                                <div className="ml-20">Home</div>
                            </Link>
                        </div>
                    )
                }
    
                <div className="flex flex-grow justify-end text-gray-50 text-lg mt-12 pr-36 box-border">
                    <Link to={{
                        pathname: "/",
                        hash: "#work"
                    }}>
                        <div className="ml-20">Work</div>
                    </Link>
                    <Link to={{
                        pathname: "/",
                        hash: "#contactme"
                    }}>
                        <div className="ml-20">Contact</div>
                    </Link>
                    <a href={resumeLink} target="_blank" rel="noopener noreferrer">
                        <div className="ml-20">Resume</div>
                    </a>
                </div>
            </div>
            <div className="flex w-full justify-end">
                <div className="w-28 h-8 border-gray-50 border-b-2"></div>
            </div>
        </div>
    )
}