firebase.initializeApp(firebaseConfig);
export default firebase

export const getAssetDownloadURL = (path) => firebase.storage().ref(path).getDownloadURL()

export const doLogin = (accessToken) => {
    return firebase.auth().signInWithEmailAndPassword(
        `${accessToken}@surojit5.github.io`,
        accessToken
    )
}

export const fetchAndMapAssetToState = async (fieldName, assetPath, setState) => {
    try {
        const assetDownloadUrl = await getAssetDownloadURL(assetPath)
        setState(prevState => ({
            ...prevState,
            [fieldName]: assetDownloadUrl
        }))
    } catch (error) {
        console.debug("Failed to download asset:", assetPath,error)
    }
}